// Pattern Printing â€“ Right-aligned Triangle

function printTriangle(n) {
  for (let i = 1; i <= n; i++) {
    let row = '';
    for (let j = 1; j <= n - i; j++) {
      row += '  ';
    }
    for (let k = 1; k <= i; k++) {
      row += '* ';
    }
    console.log(row);
  }
}

printTriangle(5);

// Output:
//         *
//       * *
//     * * *
//   * * * *
// * * * * *
